import React from 'react'

export default class Nav extends React.Component {
    render(){
        let { paths, ...props } = this.props
        return (
            <nav {...props}>
                <div className="container">
                    <ul>
                        {paths.map(path => (
                            <li key={path}><a href={path}>{path}</a></li>
                            ))}
                        </ul>
                    </div>
                </nav>
        )
    }
}



